const DemoCard = () => {
  // Group data by factory name
  const factoryData = data.reduce((acc, item) => {
    const factoryName = item.factory_name;
    if (!acc[factoryName]) {
      acc[factoryName] = {
        offeredKg: 0,
        soldKg: 0,
        unsoldKg: 0,
        totalTk: 0,
        count: 0,
      };
    }

    const grossTotal = parseFloat(item.gross_total);
    acc[factoryName].offeredKg += grossTotal;
    if (item.sold_status === 1) {
      acc[factoryName].soldKg += grossTotal;
      acc[factoryName].totalTk += grossTotal * parseFloat(item.maximum_bid);
    } else {
      acc[factoryName].unsoldKg += grossTotal;
    }
    acc[factoryName].count++;

    return acc;
  }, {});

  // Calculate average price for each factory
  Object.keys(factoryData).forEach((factoryName) => {
    const factory = factoryData[factoryName];
    if (factory.soldKg > 0) {
      factory.averagePrice = factory.totalTk / factory.soldKg;
    } else {
      factory.averagePrice = 0;
    }
  });

  return (
    <div>
      <h2>Summary Report</h2>
      <table>
        <thead>
          <tr>
            <th>Factory Name</th>
            <th>Offered Kg</th>
            <th>Sold Kg</th>
            <th>Average Price (Tk)</th>
            <th>Unsold Kg</th>
          </tr>
        </thead>
        <tbody>
          {Object.keys(factoryData).map((factoryName) => {
            const factory = factoryData[factoryName];
            return (
              <tr key={factoryName}>
                <td>{factoryName}</td>
                <td>{factory.offeredKg.toFixed(2)}</td>
                <td>{factory.soldKg.toFixed(2)}</td>
                <td>{factory.averagePrice.toFixed(2)}</td>
                <td>{factory.unsoldKg.toFixed(2)}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default DemoCard;
